<template>
  <header class="Header">
    <nav class="Header__navigation">
      <div class="Header__logo">
        <router-link to="/">
          <img class="Header__logo-image" src="/img/logo.png"/>
        </router-link>
      </div>
      <ul ref="list" :class="{'is-active': isMenuShown}" v-click-outside="onClickOutsideMenu" class="Header__list">
        <li
            v-for="(item, index) in routes"
            :key="index"
            v-click-outside="onClickOutside"
            :class="{'has-child': item.hasChild}"
            class="Header__list-link"
            @click="isShowSubmenu"
        >
          <router-link v-if="!item.hasChild && !item.anchor" :to="item.path">
            {{ item.name }}
          </router-link>
          <router-link v-else-if="item.anchor" :to="item.path" v-scroll-to="'#technologies'">
            {{ item.name }}
          </router-link>
          <span v-else-if="item.hasChild" :class="{'has-child': item.hasChild}">
            {{ item.name }}
            <font-awesome-icon :class="{top: isSubmenuShown}" :icon="['fas', 'sort-down']" />
          </span>
          <transition name="slide-fade">
            <ul v-if="item.hasChild" v-show="isSubmenuShown" class="Header__sublist">
              <li v-for="(subitem, index) in item.items" :key="index" @click="toggleMenu" class="Header__sublist-item">
                <router-link :to="subitem.path">
                  {{ subitem.name }}
                </router-link>
              </li>
            </ul>
          </transition>
        </li>
        <li class="Header__list-link--talk-to-us" @click="isShowSubmenu">
          <router-link to="/contact-us">
            <span>Contact us</span>
          </router-link>
        </li>
      </ul>
      <div class="burger" @click="toggleMenu">
        <span :class="{'active': isMenuShown}"></span>
      </div>
    </nav>
    <div class="Header__overlay" :class="{'active': isMenuShown}"></div>
  </header>
</template>
<script>
import { ROUTES } from '@/assets/routes'
import vClickOutside from 'v-click-outside'

export default {
  name: 'Header',
  data: () =>({
    isMenuShown: false,
    isSubmenuShown: false,
    routes: ROUTES
  }),
  directives: {
    clickOutside: vClickOutside.directive
  },
  methods: {
    toggleMenu () {
      this.isMenuShown = !this.isMenuShown
    },
    isShowSubmenu (e) {
      e.target.classList.contains('has-child') || !!e.target.closest('.has-child') ? (this.isSubmenuShown = !this.isSubmenuShown) : this.isMenuShown = false;
    },
    onClickOutside (e) {
      if (!(e.target.classList.contains('has-child') || !!e.target.closest('.has-child'))) this.isSubmenuShown = false
    },
    onClickOutsideMenu (e) {
      if(!e.target.classList.contains('burger')) this.isMenuShown = false
    }
  }
}
</script>
